nav
  margin-right: -5px

.nav
  &-list
    display: flex
    gap: 1rem
    list-style: none
    padding: 0
    margin: 0
    @media (max-width: $tablet)
      position: fixed
      top: 0
      right: 0
      width: 100%
      height: 100%
      justify-content: center
      flex-direction: column
      gap: 3rem
      background-color: $white
      padding: 1rem
      z-index: -1
      transition: all 0.5s ease
      transform: translateX(100%)
    &--opened
      transform: translateX(0)

  &-link
    font-weight: 400
    padding: 5px

  &-menu
    position: relative
    display: none
    border: none
    background-color: transparent
    padding: 0
    @media (max-width: $tablet)
      display: flex
    &:hover
      border: none
      background-color: transparent
      color: $black

    svg
      width: 32px
      height: 32px
      transition: all 0.5s ease

    &__open
      position: relative
      opacity: 1
      &--hidden
        opacity: 0
        transform: translateX(-100%)

    &__close
      position: absolute
      opacity: 0
      transform: translateX(100%)
      &--active
        opacity: 1
        transform: translateX(0)
